import { useEffect, useMemo, useState } from "react";
import "./styles.css";

export default function App() {
  const [orderType, setOrderType] = useState("long");
  const [risk, setRisk] = useState(1);
  const [accountBalance, setAccountBalance] = useState(0);
  const [leverage, setLeverage] = useState(1);
  const [entry, setEntry] = useState(0);
  const [exit, setExit] = useState(0);
  const [error, setError] = useState("");

  const investedAmount = useMemo(() => {
    return accountBalance * leverage;
  }, [accountBalance, leverage]);

  const calculateLossAmount = useMemo(() => {
    return accountBalance * (risk / 100);
  }, [accountBalance, risk]);

  const position = useMemo(() => {
    return (investedAmount / leverage - calculateLossAmount) / exit;
  }, [investedAmount, calculateLossAmount, leverage, exit]);

  useEffect(() => {
    if (orderType === "long" && exit >= entry) {
      setError("Entry price cannot be less than or equal to exit price.");
    } else if (orderType === "short" && entry >= exit) {
      setError("Exit price cannot be less than or equal to entry price.");
    } else {
      setError("");
    }
  }, [orderType, entry, exit]);

  return (
    <div className="App">
      <h2>Trade Risk Management</h2>
      <div className="order-type-tabs">
        <div
          className={`long ${orderType === "long" && "active"}`}
          onClick={() => setOrderType("long")}
        >
          Long
        </div>
        <div
          className={`short ${orderType === "short" && "active"}`}
          onClick={() => setOrderType("short")}
        >
          Short
        </div>
      </div>
      <div className="field-container">
        <label className="label" htmlFor="risk">
          Risk
        </label>
        <div className="input-container">
          <input
            className="input"
            name="risk"
            value={risk}
            onChange={(e) => setRisk(Number.parseFloat(e.target.value))}
            type="number"
            inputMode="decimal"
          />
          <div className="input-suffix">%</div>
        </div>
      </div>
      <div className="field-container">
        <label className="label" htmlFor="accountBalance">
          Account Balance
        </label>
        <div className="input-container">
          <input
            className="input"
            name="accountBalance"
            value={accountBalance}
            onChange={(e) =>
              setAccountBalance(Number.parseFloat(e.target.value))
            }
            type="number"
            inputMode="decimal"
          />
          <div className="input-suffix">USD</div>
        </div>
      </div>
      <div className="field-container">
        <label className="label" htmlFor="leverage">
          Leverage
        </label>
        <div className="input-container">
          <input
            className="input"
            name="leverage"
            value={leverage}
            onChange={(e) => setLeverage(Number.parseInt(e.target.value, 10))}
            type="number"
            inputMode="decimal"
            step={1}
          />
          <div className="input-suffix">X</div>
        </div>
      </div>
      <div className="field-container">
        <label className="label" htmlFor="entry">
          Entry Price
        </label>
        <div className="input-container">
          <input
            className="input"
            name="entry"
            value={entry}
            onChange={(e) => setEntry(Number.parseFloat(e.target.value))}
            type="number"
            inputMode="decimal"
          />
          <div className="input-suffix">USD</div>
        </div>
      </div>
      <div className="field-container">
        <label className="label" htmlFor="exit">
          Exit Price
        </label>
        <div className="input-container">
          <input
            className="input"
            name="exit"
            value={exit}
            onChange={(e) => setExit(Number.parseFloat(e.target.value))}
            type="number"
            inputMode="decimal"
          />
          <div className="input-suffix">USD</div>
        </div>
      </div>
      {error && <div className="error-box">{error}</div>}
      <div className="result-container">
        <p>Risk Amount:</p>
        <div className="dollar-suffix">{calculateLossAmount} USD</div>
      </div>
      <div className="result-container">
        <p>Order Value:</p>
        <div className="dollar-suffix">{accountBalance / leverage} USD</div>
      </div>
      <div className="result-container">
        <p>Position Size:</p>
        <div className="dollar-suffix">{position} BTC</div>
      </div>
    </div>
  );
}
